.ant-input,
.ant-input-affix-wrapper {
  padding: 5px 12px;
  font-size: 14px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
}
.ant-input.ant-input-lg {
  padding: 8px 12px;
}
.ant-form-item-label > label {
  height: auto;
  margin-bottom: 8px;
  color: #6b7280;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
}
.ant-form-item-explain,
.ant-form-item-extra {
  font-size: 12px;
  line-height: 14px;
}
.ant-select:not(.ant-select-customize-input) .ant-select-selector {
  height: 34px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
}
.ant-checkbox-checked .ant-checkbox-inner {
  background-color: #4f46e5;
  border-color: #4f46e5;
}
.ant-checkbox-checked:after {
  border-color: #4f46e5;
  border-radius: 4px;
}
.ant-checkbox-inner {
  border: 1px solid #d1d5db;
  border-radius: 4px;
}
.ant-checkbox-inner:after {
  margin-top: -1px;
}
.ant-checkbox-wrapper:hover .ant-checkbox-inner,
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: #4f46e5;
}
.ant-checkbox-indeterminate .ant-checkbox-inner:after {
  background-color: #4f46e5;
  border-radius: 2px;
}
.ant-btn {
  height: auto;
  padding: 7px 12px;
  color: #374151;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 4px;
  box-shadow: 0px 1px 2px rgba(6, 25, 56, 0.05);
}
.ant-btn:hover {
  color: #374151;
  background-color: #d1d5db;
  border-color: #d1d5db;
}
.ant-btn.ant-btn-primary {
  color: #ffffff;
  background-color: #4f46e5;
  border-color: #4f46e5;
}
.ant-btn.ant-btn-primary:hover {
  color: #374151;
  background-color: transparent;
}
.ant-btn.ant-btn-lg {
  height: auto;
  padding: 9px 12px;
  font-size: 16px;
  line-height: 20px;
}
.ant-btn.ant-btn-icon-only {
  width: 26px;
  height: 26px;
  padding: 1px;
}
.ant-btn + .ant-btn {
  margin-left: 12px;
}
.ant-dropdown-menu,
.ant-dropdown {
  padding: 8px 0;
  background: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  box-shadow: 0px 6px 10px -1px rgba(6, 25, 56, 0.07);
}
.ant-dropdown-menu-item {
  padding: 7px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.ant-dropdown-menu-item:hover {
  color: #4f46e5;
  background: #e0e7ff;
}
.ant-menu-vertical > .ant-menu-item {
  height: auto;
  padding: 7px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.ant-menu-vertical > .ant-menu-item:hover {
  color: #4f46e5;
  background: #e0e7ff;
}
.ant-menu-vertical > .ant-menu-item .ant-btn {
  padding: 0;
  background-color: transparent;
}
.input-text label {
  display: block;
  margin-bottom: 8px;
  color: #6b7280;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
}
.input-text span {
  display: block;
  padding: 8px 16px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
}
.ant-select-dropdown {
  padding: 12px 12px;
  background: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 4px;
  box-shadow: 0px 14px 21px -1px rgba(6, 25, 56, 0.08);
}
.table-dropdown {
  width: 120px;
}
.table-dropdown .ant-btn {
  width: 100%;
  text-align: left;
  border: none;
  box-shadow: none;
}
.ant-select-multiple .ant-select-selection-item {
  height: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 19px;
  background: #f3f4f6;
  border-radius: 4px;
}
.ant-switch-checked {
  background: #4f46e5;
}
