.table-section .ant-table-thead > tr > th {
  padding: 13px 16px;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
  border: none;
  box-shadow: inset 0px -2px 0px #e5e7eb;
}
.table-section .ant-table-tbody > tr > td {
  padding: 13px 16px;
  font-weight: 400;
  font-size: 14px;
  border: none;
  box-shadow: inset 0px -1px 0px #e5e7eb;
}
.table-section .ant-table-tbody > tr.ant-table-row:hover td {
  background-color: #f4f7fa;
}
.ant-table-thead > tr > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
  display: none;
}
.search-section {
  margin-bottom: 32px;
}
.search-section .ant-input-affix-wrapper {
  width: 240px;
}
.row-dragging {
  background: #fafafa;
  border: 1px solid #ccc;
}
.row-dragging td {
  padding: 16px;
}
.row-dragging .drag-visible {
  visibility: visible;
}
.ant-modal-root .ant-modal-mask {
  background-color: rgba(17, 24, 39, 0.5);
}
.ant-modal .ant-modal-content {
  border-radius: 8px;
}
.ant-modal .ant-modal-header {
  background: #f4f7fa;
  border-radius: 8px 8px 0 0;
}
.ant-modal .ant-modal-title {
  color: #1f2937;
  font-weight: 600;
}
.ant-modal .ant-modal-body {
  font-size: 16px;
  line-height: 20px;
}
.ant-modal .ant-modal-footer {
  border-radius: 0 0 8px 8px;
}
.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #4f46e5;
  text-shadow: none;
}
.ant-tabs-ink-bar {
  background: #4f46e5;
}
.ant-tabs-tab {
  padding: 11px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
}
.ant-tabs-bottom > .ant-tabs-nav:before,
.ant-tabs-bottom > div > .ant-tabs-nav:before,
.ant-tabs-top > .ant-tabs-nav:before,
.ant-tabs-top > div > .ant-tabs-nav:before {
  height: 1px;
  border: none;
  box-shadow: inset 0px -1px 0px #e5e7eb;
}
.tab-content-list {
  padding: 13px 0;
  box-shadow: inset 0px -1px 0px #f5f3ff;
}
.tab-content-list p {
  margin: 8px 0 0 0;
  color: #6b7280;
}
.tab-content-list h4 {
  margin: 0;
  color: #1f2937;
  font-weight: 600;
}
.tab-content-left {
  width: 40%;
  padding-right: 16px;
}
.tab-content-left h5 {
  margin: 0;
  padding-left: 14px;
  font-size: 14px;
  line-height: 24px;
}
.tab-content-right {
  width: 60%;
}
.tab-content-right .ant-checkbox-group {
  width: 100%;
}
.tab-content-right .ant-checkbox-group > label {
  width: 19%;
}
.tab-content-right > div {
  width: 19%;
}
.tab-content-right > label {
  width: 19%;
}
.tab-content-right .ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin: 0;
}
.add-role-tab .ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header {
  padding: 12px 0;
}
.add-role-tab .ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
  right: auto;
  left: 7%;
}
.add-role-tab .ant-collapse .ant-collapse-item {
  border: none;
}
.add-role-tab .ant-collapse-header {
  box-shadow: inset 0px -1px 0px #f5f3ff;
}
.add-role-tab .ant-collapse-header .ant-form-item-control-input {
  min-height: auto;
}
.add-role-tab .ant-collapse-header label {
  font-size: 16px;
}
.add-role-tab .ant-row.ant-form-item {
  margin: 0;
}
.add-role-tab .ant-collapse-content {
  border: none;
}
.add-role-tab .ant-collapse-content-box {
  padding: 0;
}
.add-role-tab .ant-collapse-content-box .tab-content-left {
  padding-left: 44px;
}
.ant-tree {
  background: #f4f7fa;
}
.ant-tree .ant-tree-node-content-wrapper {
  color: #1f2937;
  font-weight: 600;
  font-size: 14px;
}
.ant-tree .ant-tree-node-content-wrapper:hover {
  background: none;
}
.ant-tree .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-normal {
  margin: 8px 0;
  color: #6b7280;
  font-weight: 400;
  font-size: 14px;
}
.ant-tree .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-normal.ant-tree-node-selected {
  color: #4f46e5;
  font-weight: 600;
  background: none;
}
.ant-collapse {
  background: transparent;
  border: none;
}
.ant-collapse .ant-collapse-item {
  border-color: #d1d5db;
}
.ant-collapse .ant-collapse-item.ant-collapse-item-active {
  border-color: transparent;
}
.ant-collapse .ant-collapse-item + .ant-collapse-item {
  margin-top: 24px;
}
.ant-collapse-header {
  font-weight: 600;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
}
.m-0 {
  margin: 0;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.w-100 {
  width: 100%;
}
.m-b-32 {
  margin-bottom: 32px;
}
.m-b-40 {
  margin-bottom: 40px;
}
.w-480 {
  width: 480px;
}
.login-section {
  height: 100%;
  background-color: #f4f7fa;
}
.login-top-section {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 80px 0;
}
.login-popup {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 480px;
  height: 100vh;
  margin: auto;
}
.login-popup-inner {
  display: block;
  width: 480px;
  width: 100%;
  padding: 40px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(6, 25, 56, 0.06);
}
.bottom-link {
  display: block;
  width: 100%;
}
.bottom-link .ant-space {
  width: 122px;
}
.bottom-link .ant-space .ant-space-item {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.page-content {
  height: 100%;
}
.ant-layout.page {
  height: 100%;
}
.sidebar-logo {
  display: block;
  padding: 17px;
  text-align: center;
}
.sidebar-submenu {
  width: 240px;
  padding: 24px;
  background: #f4f7fa;
}
.sidebar-submenu h2 {
  margin-bottom: 16px;
  color: #1f2937;
  font-weight: 700;
  font-size: 20px;
}
.ant-layout {
  background: #fff;
}
.ant-layout-sider {
  background: #fff;
}
.ant-layout-header {
  height: auto;
  padding: 0 35px;
  background: #fff;
}
.ant-page-header {
  padding: 16px 20px;
}
.ant-page-header-heading-left {
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
}
.ant-page-header-heading-title {
  display: block;
}
.ant-layout-content {
  padding: 32px;
}
.ant-page-header-heading-sub-title {
  color: #6b7280;
  font-weight: 400;
  font-size: 13px;
}
.ant-menu.ant-menu-inline-collapsed > .ant-menu-item,
.ant-menu.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-item,
.ant-menu.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-submenu > .ant-menu-submenu-title,
.ant-menu.ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title {
  height: auto;
  padding: 20px;
  line-height: inherit;
  white-space: initial;
  text-align: center;
}
.generate-box {
  padding: 42px 64px;
  border: 1px solid #e5e7eb;
}
.generate-box-content {
  width: 800px;
  margin-right: 110px;
}
.drag-content .ant-table-tbody tr {
  display: flex;
  width: 480px;
  margin-bottom: 16px;
  border: 1px solid #e5e7eb;
  border-radius: 4px;
}
.drag-content .ant-table-tbody tr td {
  border: none;
}
.drag-content .ant-table-tbody tr td:nth-child(2) {
  width: calc(100% - 46px);
}
.drag-content .ant-table-tbody .drag-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title-with-border {
  margin-top: 50px;
  margin-bottom: 16px;
  padding-bottom: 12px;
  padding-left: 52px;
  box-shadow: inset 0px -2px 0px #e5e7eb;
}
